import React, { useEffect } from "react";
import { Form, Input, Switch, Radio } from "antd";

const DesignConfiguration = ({ changeConfiguration, configuration }) => {
  const [form] = Form.useForm();
  useEffect(() => {
    try {
      form.setFieldsValue(JSON.parse(configuration));
    } catch (error) {
      console.error("configuration", error);
    }
  }, []);

  const onFormLayoutChange = (changedValues, allValues) => {
    changeConfiguration(JSON.stringify(allValues));
  };

  const formItemLayout = {
    labelCol: { span: 24 },
    wrapperCol: { span: 24 },
  };

  return (
    <>
      <Form
        {...formItemLayout}
        layout="vertical"
        form={form}
        onValuesChange={onFormLayoutChange}
      >
        <Form.Item label="" name="size" de>
          <Radio.Group>
            <Radio.Button value="small"></Radio.Button>
            <Radio.Button value="middle"> </Radio.Button>
            <Radio.Button value="large"></Radio.Button>
          </Radio.Group>
        </Form.Item>
        <Form.Item label="" name="placeholder">
          <Input />
        </Form.Item>
        <Form.Item label="" name="allowClear" valuePropName="checked">
          <Switch></Switch>
        </Form.Item>
      </Form>
    </>
  );
};

DesignConfiguration.propTypes = {};

export default DesignConfiguration;
